import React, { lazy } from 'react';
import { createHashRouter } from "react-router-dom";
import LoginRoutes from './login';
import { BankOutlined, CopyOutlined, CodeOutlined, AreaChartOutlined, AntDesignOutlined, UserOutlined, KeyOutlined, ApiOutlined } from '@ant-design/icons';

const ErrorPage = lazy(() => import('./../error-page'));
const AllLayout = lazy(() => import('./../layout/AllLayout'));
const HomeLayout = lazy(() => import('./../layout/HomeLayout'));
const PageAnalysis = lazy(() => import('../pages/pageIndex/PageAnalysis'));
const PageWorkplace = lazy(() => import('../pages/pageIndex/PageWorkplace'));
const PageCopy = lazy(() => import('../pages/pageCopy/PageCopy'));
const PageCode = lazy(() => import('../pages/pageCode/PageCode'));
const PageEcharts = lazy(() => import('../pages/pageEcharts/PageEcharts'));
const PageDraggableTabs = lazy(() => import('../pages/pageAntd/PageDraggableTabs'));
const PageUserInfo = lazy(() => import('../pages/pageUser/PageUserInfo'));
const PageUsers = lazy(() => import('../pages/pageUser/PageUsers'));
const PageNormal = lazy(() => import('../pages/pageAccess/PageNormal'));
const PageMiddle = lazy(() => import('../pages/pageAccess/PageMiddle'));
const PageAdmin = lazy(() => import('../pages/pageAccess/PageAdmin'));
const Page403 = lazy(() => import('../pages/pageCommon/Page403'));
const Page404 = lazy(() => import('../pages/pageCommon/Page404'));
const Page500 = lazy(() => import('../pages/pageCommon/Page500'));
const PageMockUsers = lazy(() => import('../pages/pageMock/PageMockUsers'));

export const routerList = [
  {
    path: "/",
    redirect: "/home/analysis",
    errorElement: <ErrorPage />,
    element: <AllLayout />,
    children: [
      {
        path: "/",
        redirect: "/home/analysis",
        element: <HomeLayout />,
        // 
        requiresAuth: true,
        children: [
          {
            path: '/home',
            redirect: '/home/analysis',
            meta: {
              title: 'dashboard',
              icon: <BankOutlined />,
            },
            children: [
              {
                path: '/home/analysis',
                element: <PageAnalysis />,
                requiresAuth: true,
                // [](https://ant-design.antgroup.com/components/watermark-cn#watermark)
                //  Boolean  Array  Boolean  content  config.watermark Array  content
                watermark: true,
                meta: {
                  title: '',
                }
              },
              {
                path: '/home/workplace',
                element: <PageWorkplace />,
                requiresAuth: true,
                watermark: ['aaa', ''],
                meta: {
                  title: '',
                }
              }
            ]
          },
          {
            path: '/pageCopy',
            element: <PageCopy />,
            watermark: true,
            isHideBreadcrumb: true,
            meta: {
              title: '',
              icon: <CopyOutlined />,
            }
          },
          {
            path: '/pageCode',
            element: <PageCode />,
            isHideBreadcrumb: true,
            isHideTabs: true,
            meta: {
              title: '',
              icon: <CodeOutlined />,
            }
          },
          {
            path: '/pageEcharts',
            element: <PageEcharts />,
            isHideBreadcrumb: true,
            meta: {
              title: 'echarts',
              icon: <AreaChartOutlined />,
            }
          },
          {
            path: '/antd',
            redirect: "/antd/pageDraggableTabs",
            meta: {
              title: 'antd',
              icon: <AntDesignOutlined />,
            },
            children: [
              {
                path: '/antd/pageDraggableTabs',
                element: <PageDraggableTabs />,
                meta: {
                  title: 'Tabs',
                }
              },
            ]
          },
          {
            path: '/user',
            redirect: '/user/userInfo',
            meta: {
              title: '',
              icon: <UserOutlined />,
            },
            children: [
              {
                path: '/user/userInfo',
                element: <PageUserInfo />,
                requiresAuth: true,
                watermark: true,
                meta: {
                  title: '',
                }
              },
              {
                path: '/user/users',
                element: <PageUsers />,
                requiresAuth: true,
                watermark: true,
                meta: {
                  title: '',
                }
              }
            ]
          },
          {
            path: '/mock',
            redirect: '/mock/users',
            meta: {
              title: 'mock',
              icon: <ApiOutlined />,
            },
            children: [
              {
                path: '/mock/users',
                element: <PageMockUsers />,
                requiresAuth: true,
                watermark: true,
                meta: {
                  title: 'mock',
                }
              }
            ]
          },
          {
            path: '/access',
            redirect: '/access/pageNormal',
            meta: {
              title: '',
              icon: <KeyOutlined />,
            },
            children: [
              {
                path: '/access/pageNormal',
                element: <PageNormal />,
                requiresAuth: true,
                watermark: true,
                access: 'normal',
                meta: {
                  title: 'normal',
                }
              },
              {
                path: '/access/pageMiddle',
                element: <PageMiddle />,
                watermark: true,
                access: 'middle',
                meta: {
                  title: 'middle',
                }
              },
              {
                path: '/access/pageAdmin',
                element: <PageAdmin />,
                requiresAuth: true,
                watermark: true,
                access: 'admin',
                meta: {
                  title: 'admin',
                }
              }
            ]
          },
          {
            path: '/403',
            element: <Page403 />,
            hideInMenu: true,
            isHideBreadcrumb: true,
            meta: {
              title: '403',
            }
          },
          {
            path: '/404',
            element: <Page404 />,
            hideInMenu: true,
            isHideBreadcrumb: true,
            meta: {
              title: '404',
            }
          },
          {
            path: '/500',
            element: <Page500 />,
            hideInMenu: true,
            isHideBreadcrumb: true,
            meta: {
              title: '500',
            }
          }
        ],
      },
      ...LoginRoutes
    ]
  }
];

const router = createHashRouter(routerList);

export default router;
